<template>
    <div>
        <!-- Table All Informasi -->
        <vx-card>
        <vs-table stripe search pagination  max-items="10" :data="informasi">
            <template slot="header" class="mb-5">
                <h1 class="mr-2">Data Informasi</h1>
                <vx-tooltip text="Tambah Informasi" style="display:inline">
                    <vs-button @click="btnAddInformasi" radius icon="create" color="success" type="flat" size="large"></vs-button>
                </vx-tooltip>
            </template>

            <template slot="thead">
                <vs-th sort-key="img" class="bg-primary text-white">
                Foto
                </vs-th>
                <vs-th sort-key="caption" class="bg-primary text-white">
                Caption
                </vs-th>
                <vs-th sort-key="link" class="bg-primary text-white">
                Link
                </vs-th>
                <vs-th sort-key="aksi" width="80px" class="bg-primary text-white text-center">
                <p class="w-full">Aksi</p>
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode" v-for="items in data" >
                    <vs-td :data="items.img">
                        <img class="img-view" :src="items.img" alt="img-informasi">
                    </vs-td>
                    <vs-td :data="items.caption">
                        {{items.caption}}
                    </vs-td>
                    <vs-td :data="items.link">
                        {{items.link}}
                    </vs-td>
                    <vs-td :data="items.aksi" class="text-center">
                    <vx-tooltip text="Ubah Informasi" style="display:inline">
                        <vs-button @click="editData(items.kode, items.caption, items.link, items.img)" class="mr-1" color="warning" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                    </vx-tooltip>
                    </vs-td>
                </vs-tr>
            </template>
        </vs-table>
        </vx-card>

        <!-- Add Informasi -->
        <vs-popup class="holamundo"  title="Tambah Informasi" :active.sync="addNew">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Caption" v-validate="'required'" name="form.caption" data-vv-validate-on="blur" v-model="form.caption" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Link" v-validate="'required'" name="form.link" data-vv-validate-on="blur" v-model="form.link" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Foto</small><br>
                        <img class="img-default my-2" :src="form.img">
                        <vs-input type="file" @change="handleImage" accept="image/*"  />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateFormAdd" class="mr-3 mb-2" @click=" addData()">Simpan</vs-button>
                    </div>
                </div>
        </vs-popup>

        <!-- Edit Wilayah -->
        <vs-popup class="holamundo"  title="Edit Informasi" :active.sync="Edit">
                 <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Caption" v-validate="'required'" name="form.caption" data-vv-validate-on="blur" v-model="form.caption" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Link" v-validate="'required'" name="form.link" data-vv-validate-on="blur" v-model="form.link" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Foto</small><br>
                        <img class="img-default my-2" :src="form.img">
                        <vs-input type="file" @change="handleImage" accept="image/*"  />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateFormEdit" class="mr-3 mb-2" @click="updateData(form.kode)">Simpan</vs-button>
                    </div>
                </div>
        </vs-popup>
    </div>
</template>

<script>

export default {
    data() {
        return {
            publicPath: process.env.BASE_URL,
            addNew:false,
            Edit:false,
            informasi:[],
            selectedDate:new Date(),
            form: {
                kode:'',
                caption:'',
                link:'',
                img:null
            }

        }
    },

    computed: {
        validateFormAdd() {
            return !this.errors.any() 
            && this.form.caption != ''
            && this.form.link != '';
        },

        validateFormEdit() {
            return !this.errors.any() 
            && this.form.caption != ''
            && this.form.link != ''
        },
    },

    created() {
       this.loadData();
    },

    methods:{

        loadData(){
            this.$vs.loading();
            this.$http.get('agen/web/informasi')
            .then(response => {
                this.informasi = response.data.message
                // console.log(response);
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        },

        btnAddInformasi(){
            this.addNew = true,
            this.form.caption = ''
            this.form.link = ''
            this.form.img = null
        },

        handleImage(e){
            const selectedImage = e.target.files[0];
            this.createBase64Image(selectedImage);
        },

        createBase64Image(fileObject){
            const reader = new FileReader();

            reader.onload = (e) => {
                this.form.img = e.target.result;
            };
            reader.readAsDataURL(fileObject);
        },

        addData(){
            this.$vs.loading();
            const postData = {
                caption: this.form.caption,
                link: this.form.link,
                img: this.form.img,
            }
            // console.log(postData);
            this.$http.post('agen/web/informasi/create', postData)
            .then(response => {
                this.$vs.loading();
                if(response.data.status == "SUKSES"){
                    this.form.caption = '',
                    this.form.link = '',
                    this.form.img = null,
                    this.addNew = false

                    this.$swal.fire({
                    title: 'Success',
                    text: response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                    
                    )
                })
            .finally(() => {this.$vs.loading.close()})
        },

        editData(kode, caption, link, img){
            this.Edit = true
            this.form.kode = kode
            this.form.caption = caption 
            this.form.link = link 
            this.form.img = img 
        },

        updateData(kode){
            this.$vs.loading();
            const updtData = {
                kode: kode,
                caption: this.form.caption,
                link: this.form.link,
                img: this.form.img,
            }
            this.$http.post('agen/web/informasi/update' , updtData)
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.form.kode = '',
                    this.form.caption = ''
                    this.form.link = ''
                    this.form.img = null
                    this.Edit = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        }
    }

}
</script>

<style lang="stylus">
    .img-view
        width :70px
        border-radius: 8px
    
    .img-default
        width :200px
        border-radius: 8px

    .swal2-container 
        z-index: 100000;
        
</style>